import React from 'react';
import './App.css';
import ReactPageScroller from "react-page-scroller";
import TopPageComponent from "./components/top-page/top-page.component";
import ComingSoonComponent from "./components/comginsoon/coming-soon.component";
import NavbarComponent from "./components/navbar/navbar.component";
import ExperienceComponent from "./components/experience/experience.component";

// import 'bootstrap/dist/css/bootstrap.min.css';


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currPage: 0
        }
    }

    handlePageChange = number => {
        this.setState({currPage: number});
    }

    scrollDown = _ => {
        this.setState({currPage: this.state.currPage + 1})
    }

    setPage = page => {
        console.log("Going to", page)
        this.setState({currPage: page})
    }


    render() {
        return (
            <div className="App">
                {this.state.currPage !== 0 &&
                <NavbarComponent links={['Experience', 'Projects', 'Certifications']} changePage={this.setPage}
                                 style={{width: '100vw'}} curr={this.state.currPage}/>}
                <ReactPageScroller
                    pageOnChange={this.handlePageChange}
                    customPageNumber={this.state.currPage}
                >
                    <TopPageComponent scrollDown={this.scrollDown}/>
                    <ExperienceComponent/>
                    <ComingSoonComponent/>
                    <ComingSoonComponent/>
                </ReactPageScroller>
            </div>
        );
    }
}

export default App;