import React from "react";
import {Navbar, NavbarBrand} from "react-bootstrap";
import './styles.css'
import NavLink from "react-bootstrap/NavLink";

export default class NavbarComponent extends React.Component {

    render() {
        return (
            <div className={'navbar-container'}>
                <Navbar className={'navbar'} expand={'lg'}>
                    <NavbarBrand><img
                        src={'https://avatars2.githubusercontent.com/u/47828215?s=460&u=92b27caf8ef0a02ea67f9702de16cb0ce950909f&v=4'}
                        className={'navbar-image'} alt={''} onClick={() => {
                        this.props.changePage(0)
                    }}/></NavbarBrand>
                    <Navbar.Collapse className="justify-content-end">
                        {this.props.links.map((link, i) => {
                            return (<NavLink active={this.props.curr===i+1} className={'nav-link'} onClick={() => {
                                this.props.changePage(i + 1)
                            }}>{link}</NavLink>)
                        })}
                    </Navbar.Collapse>
                </Navbar>
            </div>
        )
    }
}